import { useState } from "react";
import { GoogleLogin } from "react-google-login";
import { afterGoogleAuth, googleAuth, register } from "../../utils/servicesApi";

const GoogleForm = () => {
  const [form, setForm] = useState({ email: "", password: "" });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    register(form).then(console.log).catch(console.log);
  };

  const handleGoogleAuth = () => {
    afterGoogleAuth().then(console.log).catch(console.log);
  };

  const responseGoogle = (response) => {
    console.log(response);
  };

  return (
    <form onSubmit={handleSubmit}>
      <label>
        email
        <input
          type="text"
          name="email"
          value={form.email}
          onChange={handleChange}
        />
      </label>
      <label>
        password
        <input
          type="text"
          name="password"
          value={form.password}
          onChange={handleChange}
        />
      </label>
      <div>
        <button onClick={handleGoogleAuth} type="button">
          Google
        </button>
        <button type="submit">Register</button>
      </div>
      {/* <GoogleLogin
        buttonText="Login"
        onSuccess={responseGoogle}
        onFailure={responseGoogle}
        cookiePolicy={"single_host_origin"}
        isSignedIn={true}
      /> */}
      <a
        href="https://kidslike-v1-backend.goit.global/auth/google"
        aria-label="google button"
      >Google</a>
    </form>
  );
};

export default GoogleForm;
